.section-15 {
  h2 {
    margin-bottom: 57px;
  }

  padding-top: 105px;
  padding-bottom: 120px;

  .pic-block {
    color: #fff;

    h3 {
      font-size: 22px;
      font-weight: 700;
      line-height: 32px;
    }

    ul {
      li {
        position: relative;
        font-size: 16px;
        font-weight: 400;
        line-height: 30px;
        margin-top: 10px;
        padding-left: 14px;

        &::before {
          content: '';
          position: absolute;
          width: 6px;
          height: 6px;
          background-color: #fec400;
          border-radius: 50%;
          left: 0;
          top: 13px;
        }
      }
    }

    .horizontal {
      margin-left: 30px;

      .block1 {
        margin-bottom: 30px;

        div {
          padding-left: 52px;
          padding-top: 99px;
        }
      }

      .block2 {
        div {
          padding-top: 79px;
          padding-left: 51px;
        }

      }

      .block3 {
        margin-top: 30px;
      }
    }

    .vertical {


      div {
        padding-left: 51px;
        padding-top: 50px;
      }

      .section-4 .vertical li {
        margin-bottom: 30px;
        font-size: 18px;
        line-height: 30px;
        color: #fff;
      }
    }
  }


}